body {
	background: #f5d9d5;
	font-family: 'Nunito', sans-serif;
}

.blurb h2 {
	color: #ea1c2c;
	font-weight: 100;
	font-size: 2.5rem;
}

.blurb p {
	color: #f498b8;
	font-weight: 100;
	font-size: 1.125rem;
	line-height: 2;
}

.blurb h4 {
	color: #f498b8;
	font-weight: 100;
	line-height: 2;
}

.content {
	margin-top: 100px;
	margin-bottom: 20px;
}

.inline-link {
	color: #f44336;
}

.inline-link:hover {
	color: #f44336;
	text-decoration: none;
}

.router-link-text:hover {
	color: #f44336;
	text-decoration: none;
}

.router-link:hover {
	text-decoration: none;
}

#mainNavbar {
	font-size: 1.5rem;
	font-weight: 100;
}

#mainNavbar .nav-link {
	color: white;
}

#mainNavbar .nav-link:hover {
	color: #ea1c2c;
}

#headingGroup span {
	color: #ea1c2c;
}

#headingGroup h1 {
	font-weight: 100;
	font-size: 4rem;
}

.navbar.scrolled {
	background: rgb(222, 192, 222);
	transition: background 500ms;
}

@media (max-width: 1200px) {
	#headingGroup h1 {
		font-size: 3rem;
	}
}
